<template>
    <v-container id="template" >
        <v-btn id="print" class="blue white--text mb-6" @click="printReport()">Print</v-btn>
        <v-btn id="print" class="ml-4 red white--text mb-6" to="/produksiharian" link>Back</v-btn>
       
       <template id="template">
           <pre class="text-left" style="font-size:12px"><strong>Produksi Tanggal : {{induk["Tanggal"]}}</strong></pre>
            <table id="table" width="1009px" >
                <thead>
                    <tr style="font-size:10px">
                        <th id="td" rowspan="2">Kode Barang</th>
                        <th id="td" colspan="2">Jumlah</th>
                        <th id="td" rowspan="2">Etiket Depan</th>
                        <th id="td" rowspan="2">Etiket Belakang</th>
                        <th id="td" rowspan="2">Etiket Tambahan</th>
                        <th id="td" rowspan="2">Botol</th>
                        <th id="td" rowspan="2">Tutup</th>
                        <th id="td" rowspan="2">Jumlah Pemakaian</th>
                        <th id="td" rowspan="2">Dos</th>
                        <th id="td" rowspan="2">Label Dos</th>
                        <th id="td" rowspan="2">Jumlah Pemakaian</th>
                        <th id="td" rowspan="2">Isi</th>
                        <th id="td" rowspan="2">Jumlah Isi</th>
                    </tr>
                    <tr style="font-size:10px">
                        <th id="td">Dosen</th>
                        <th id="td">Botol</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in induk" :key='item.id'>
                        <td id="td" style="font-size:8pt;padding-left:0.1cm;">{{item["Kode Barang"]}}</td>
                        <td id="td" style="font-size:8pt;width:1.3cm;text-align:right;padding-right:0.1cm;">{{item["JmlDs"]}}</td>
                        <td id="td" style="font-size:8pt;padding-right:0.1cm;width:1.2cm;" class="text-right">{{item["JmlBotol"]}}</td>
                        <td id="td" style="font-size:8pt; width:100px;padding-left:0.1cm">{{item["NmEtiketDepan"].substring(0,12)}}</td>
                        <td id="td" style="font-size:8pt; width:90px;padding-left:0.1cm">{{item["NmEtiketBelakang"].substring(0,12)}}</td>
                        <td id="td" style="font-size:8pt;padding-left:0.1cm;">{{item["NmEtiketTambahan"]}}</td>
                        <td id="td" style="font-size:8pt; width:90px;padding-left:0.1cm;">{{item["NmBotol"].substring(0,11)}}</td>
                        <td id="td" style="font-size:8pt; width:100px;padding-left:0.1cm;">{{item["NmTutup"].substring(0,16)}}</td>
                        <td id="td" style="font-size:8pt;padding-right:0.1cm;" class="text-right">{{item["Jumlah"]}}</td>
                        <td id="td" style="font-size:8pt;width:60px;padding-left:0.1cm;">{{item["NmDos"]}}</td>
                        <td id="td" style="font-size:8pt;width:90px;padding-left:0.1cm;">{{item["NmLabel"]}}</td>
                        <td id="td" style="font-size:8pt;padding-right:0.1cm;" class="text-right">{{item["JumlahDos"]}}</td>
                        <td id="td" style="font-size:8pt;width:90px;padding-left:0.1cm;">{{item["NmIsi"]}}</td>
                        <td id="td" style="font-size:8pt;padding-right:0.1cm;width:2cm;" class="text-right">{{item["JumlahIsi"] | currency}}</td>
                        
                    </tr>
                </tbody>
            </table>
       </template>
    </v-container>
</template>
<script>
    export default {
        data() {
            return {
                keyword: '',
                load: false,
                induk: [],
            }
        },
        methods: {
            getData() {
                var uri = this.$apiUrl + 'produksis/laporanproduksiharian/' + this.$route.params.kode
                this.$http.get(uri).then(response => {
                    this.induk = response.data
                    this.induk["Tanggal"] = response.data[0]["Tanggal"]
                    console.log(this.induk)
                })
            },

            
            printReport() {
                window.print()
            },

            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },
        },
        mounted() {
            this.getData();
        },
    }
</script>
<style>
    @media print{
        #print{
            display: none;
        }
        #template{
            background-color: white
        }
    }
    
    #table, #th, #td {
        border: 1px solid black;
        border-collapse: collapse;
    }
</style>